/**
 * axios
 * 
 */
import Vue from 'vue'
import axios from 'axios';
import router from '../router';
import store from '../store/index';
import qs from 'qs'; // qs
import base from '@/api/base'; // 
import {
    Message
} from 'element-ui';

/**
 * 
 * 
 */
const tip = msg => {
    Message({
        message: msg,
        type: 'warning'
    });
}

/**
 * 
 * 
 */
const toLogin = () => {
    router.replace({
        path: '/login',
        query: {
            redirect: router.currentRoute.fullPath
        }
    });
}

/**
 * 
 * @param {Number} status 
 */
const errorHandle = (status, other) => {
    // 
    switch (status) {
        // 401: 
        case 401:
            toLogin();
            break;
        // 403 token
        // token
        case 403:
            // tip('');
            sessionStorage.removeItem('okbix-token')
            // setTimeout(() => {
            //     toLogin();
            // }, 1000);
            break;
        // 404
        case 404:
            tip('');
            break;
        default:
            console.log(other);
    }
}

// axios
var instance = axios.create({
    timeout: 1000 * 12
});
// post
instance.defaults.headers.post['Content-Type'] = 'application/json';
/**
 * 
 * tokentoken
 */
instance.interceptors.request.use(
    config => {
        // pc tokenBearer
        const prefixToken = 'Bearer '
        const token = sessionStorage.getItem('okbix-token')
        token && (config.headers.token = `${prefixToken}${token}`);
        return config;
    },
    error => Promise.error(error)
)

// 
instance.interceptors.response.use(
    // 
    res => {
        if(res.status === 200){
            if(res.data.code === 0){
                return Promise.resolve(res)
            }else{
                if(res.data.msg && res.data.msg.includes('token')){
                    tip('');
                    sessionStorage.removeItem('okbix-token')
                    // setTimeout(() => {
                    //     toLogin()
                    // }, 500)
                }else{
                    tip(res.data.msg)
                }
            }
        }else{
            return Promise.reject(res)
        }
    },
    // 
    error => {
        const {
            response
        } = error;
        if (response) {
            // 2xx
            errorHandle(response.status, response.data.message);
            return Promise.reject(response);
        } else {
            console.log(error)
            // tip('')
        }
    }
);

const ajax = function (type, url, query){
    let option = {
        method: type,
        url: url,
        baseURL: base.okbix,
    }

    if(type.toUpperCase() === 'GET'){
        option.params = query
    }else if(type.toUpperCase() === 'POST'){
        option.data = query
    }
    return instance(option)
};

export default ajax